.App {
  text-align: center;
  font-family: Arial;

}

.filter-list {

}

.header {
  /*background: url('./curb.jpg');*/
  margin-top: 0px;
  margin-bottom: 0px;
  padding: 0;
  -ms-flex-line-pack: center;
      align-content: center;
  text-align: center;
  background: url('./Concrete-Texture.jpg');

}

.curbalert {
  /*
  background: url('./CurbAlert.png'); 
  */

  width: 60vw;
  -ms-flex-item-align: center;
      align-self: center;
  border-radius: 10px;
  border-color: 20px;
  text-align: center;
  background: url('./CurbAlert.png');
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-left: 15vw;


}

h1 {
  color: white;
  text-shadow: 1px 1px 10px #FF0000;
  font-size: 4em;
  text-align: center;
  font-weight: regular;
  font-family: "Lucida Grande"
}

h2 {
  color: white;
  font-style: bold;
  text-shadow: 2px 2px 10px black;
}

img {
  -ms-flex-item-align: middle;
      align-self: middle;
  width: 100%;
  height: 100%;

}

.newItemFormAll {
  display: grid;
  grid-template-columns: 30vw 70vw;
  grid-template-rows: 23vw;
  border-bottom: 2px solid black;
  margin-bottom: 2vw;
}


.addPhoto { 
  font-family: Arial;
  font-weight: bold;
  font-size: 1.5vw;  
  padding: 8vw;
  padding-top:1vw;
  padding-bottom: 8vw;


}

.newItemForm {
  padding-top: 2vw;
  padding-right: 5vw;
}



.submitButton 
{margin-bottom: 5vw}

.list {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0.5vw;

}

.searchInput {
  width: 20vw;
  font-size: 1.5vw;
}

.dropDown {
  font-size: 1.5vw;
  margin-top: 3px;
  width: 18vw;
}



.item-box {
    width: 28vw;
    height: 30vw;
    border: 2px solid #1c6600;
    padding: 2vw;
    margin: 2vw;
    background-color: white;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 1vw;

    /*
    border: 4px solid green;
    padding: 0vw;
    margin: 1vw;
    background-color: white;
    box-shadow: 5px 5px;
    border-radius: 0.5vw; */

}

.item-pic {
  width: 18vw;
  height: 18vw;
  margin-left: 3vw;
  margin-bottom: 2vw;
  text-align: center;
  border: 2px solid black;
 

  /*
  width: 27.6vw;
  height: 29.6vw;
  border-radius: 0.5vw;
  margin-top: 0vw;
  margin-bottom: -5vw;
  margin-left: 0;
  margin-right: 0;
  float:left;
  border: 0; */

}

.parameters {
  text-align: middle;
  border-top: 3px solid black;
  border-bottom: 3px solid black;
  font-size: 2.5vw;
  font-family: "Lucida Grande";
  background-color: #000;
  height: 60px;

}



.parameterselections {
  /* height: 10vw; */

}

.label{
  font-size: 12pt;
  margin: 5px;
}

.filterBox {
  float: left;
  padding-left: 2%;

}

.searchInput {
width: 30vw;
height: 36px;
margin: 10px;

}

.sortArea {
  padding-left: 2%;
  float:left;

}

.item-description {
    margin-bottom: 0.8vw;
    font-size: 1.8vw;
    font-family: "Lucida Grande";
    font-weight: bold;
    text-align: center;

    /*
    margin-bottom: -2.6vw;
    background-color: black;
    color: white;
    */
    

}
.item-time {
  text-align: right;
  float: right;
  margin-right: 2vw;
  font-size: 1.2vw;

  /*
  background-color: black;
  color: white;
  */

}

.item-distance {
  margin: 0;
  margin-top: 0;
  padding-left: 2vw;
  text-align: left;
  font-size: 1.2vw;
  float:left;

  /*
  background-color: black;
  color: white;
  */
}

@media (max-width: 600px) {
  .list {
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 0.5vw;

}
  .item-box {
    width: 90vw;
    height: 96.4vw;
    padding: 6vw;
    margin: 6vw;

  }

  .item-pic {
  width: 60vw;
  height: 60vw;
  margin-left: 9vw;
  margin-bottom: 6vw;
}

.item-description {
    margin-bottom: 2.4vw;
    font-size: 5.4vw;

}
.item-time {
  margin-right: 6vw;
  font-size: 3.6vw;

}

.item-distance {

  padding-left: 6vw;
  text-align: left;
  font-size: 3.6vw;
}

}










.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



